.App {
  text-align: center;
}

.SlideSelector {
  display: flex;
  flex-direction: column;
  text-align: left;
  margin: 30px 0 0 50px;
}

.ButtonContainer {
  flex: 1;
  display: flex;
  
}

.CategoryButton {
  width: 60px;
  margin:10px 0px;
  padding: 10px 10px;
  background-color: lightgray;
  border: black 0.5px solid;
}

.BackButton {
  width: 100px;
  margin:10px 0px;
  padding: 10px 10px;
  background-color: lightgray;
  border: black 0.5px solid;
}

.CategoryButton:hover {
  background-color: deepskyblue;
}

.CategoryButton:focus {
  background-color: lightgreen;
}

.PlotContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.SVG {
  margin-bottom: 10px;
}